/**
 * Manages filters.
 */
export default class Filters {
    constructor() {
    }
    static apply(obj, params) {
        var cl = requirejs("jx/core/filters/" + params.filter);
        cl.apply(obj, params);
    }
    static cancel(obj, filterName) {
        var cl = requirejs("jx/core/filters/" + filterName);
        cl.cancel(obj);
    }
}
